import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/views/Home'
import Admin from '@/views/Admin'
import Login from '@/views/Login'
import Register from '@/views/Register'
import Users from '@/views/Users'
import UserInfo from '@/views/UserInfo'

Vue.use(Router)

const router = new Router({
  // mode: 'history',
  routes: [{
      path: '/',
      name: 'Admin',
      component: Admin,
      redirect: '/users',
      meta: {
        isLogin: true, // 必须登录
      },
      children: [
        {
          path: '/users',
          name: 'Users',
          component: Users,
          meta: {
            isLogin: true, // 必须登录
          },
        },
        {
          path: '/info',
          name: 'UserInfo',
          component: UserInfo,
          meta: {
            isLogin: true, // 必须登录
          },
        }
      ]
    },
    {
      path: '/login',
      name: 'Login',
      component: Login,
      meta: {
        notLogin: true, // 必须不登录
      }
    },
    {
      path: '/reg',
      name: 'Register',
      component: Register,
      meta: {
        notLogin: true, // 必须不登录
      }
    }
  ]
})


export default router



