<template>
    <Content class="content">
        <Breadcrumb>
            <BreadcrumbItem to="/">首页</BreadcrumbItem>
            <BreadcrumbItem to="/users">用户列表</BreadcrumbItem>
        </Breadcrumb>
        <Content class="content content-item">
            <Input search enter-button="搜索" v-model='search' placeholder="搜索用户" />
        </Content>
        <Content class="table-users content">
             <Table border stripe :columns="columns" :data="fUsers"></Table>
        </Content>
        <Content class="content page">
            <Page :total="10" :page-size="10" :current.sync="current" />
        </Content>
    </Content>
</template>

<script>
import axios from "axios";
export default {
  data() {
    return {
      columns: [
        {
          title: "ID",
          key: "id"
        },
        {
          title: "头像",
          width: 100,
          render: h =>
            h("Avatar", {
              props: {
                src: "https://www.thenewstnp.com/img/IMG_0020.JPG"
              }
            })
        },
        {
          title: "Name",
          key: "name"
        },
        {
          title: "Email",
          key: "email"
        },
        {
          title: "Phone",
          key: "phone"
        },
        {
          title: "Website",
          key: "website"
        },
        {
          title: "管理",
          render: (h, parms) =>
            h(
              "Button",
              {
                props: {
                  size: "small",
                  type: "warning"
                },
                on: {
                  click: parms => {
                    console.log(parms);
                  }
                }
              },
              "删除"
            )
        }
      ],
      data: [],
      current: 1,
      search: ""
    };
  },
  created() {
    axios
      .get("http://localhost:3000/apis/users.json")
      .then(({ data }) => {
        console.log(data);
        for (const key in data) {
          if (data.hasOwnProperty(key)) {
            this.data.push(data[key]);
          }
        }
      })
      .catch(err => {
        console.log(err);
      });
  },
  watch: {
    current(newValue) {
      this.data = [];
      axios
        .get(`/apis/users.json`)
        .then(({ data }) => {
          // console.log(data);
          for (const key in data) {
            if (data.hasOwnProperty(key)) {
              this.data.push(data[key]);
            }
          }
        })
        .catch(err => {
          console.log(err);
        });
    }
  },
  computed: {
    fUsers() {
      return this.data.filter(k => {
        return k.name.indexOf(this.search) > -1;
      });
    }
  }
};
</script>

<style scoped>
.content {
  padding: 0;
}
.content.content-item {
  margin: 20px 20px;
  padding: 0 10px;
}
.content.page {
  display: flex;
  justify-content: center;
  align-items: center;
}
</style>


