<template>
    <Sider>
        <Menu theme="light" @on-select="menuSelect" active-name='/'>
             <MenuGroup title="用户管理">
                 <MenuItem name="/">
                     <Icon type="md-document" />
                     报表管理
                 </MenuItem>
                 <MenuItem name="/users">
                     <Icon type="md-document" />
                     查询用户
                 </MenuItem>
                 <MenuItem name="/info">
                     <Icon type="md-document" />
                     状态管理
                 </MenuItem>
             </MenuGroup>
        </Menu>
        </Sider>
</template>

<script>
export default {
    methods: {
        menuSelect(value) {
            this.$router.push(value)
        }
    }
};
</script>

<style scoped>
.ivu-layout-sider {
    height: 100%;
}
</style>


