<template>
    <div class="layout">
        <Header />
        <Layout>
                <Sider />
                <Content>
                    <router-view></router-view>
                </Content>
        </Layout>
    </div>
</template>

<script>
import Header from '../components/Header'
import Sider from '../components/Sider'
export default {
  components: {
      Header,
      Sider
  }
};
</script>

<style>
.layout {
  height: 100%;
  background: #f2f2f2;
}
.ivu-menu {
  height: 100%;
}
.ivu-layout {
  height: 100%;
  display: flex;
  flex-flow: row nowrap;
}
.ivu-layout-content {
    padding-left: 40px;
}
.layout-logo {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  float: left;
  position: relative;
  top: 12px;
  left: 0px;
  background: #5b6270 url(https://www.thenewstnp.com/img/IMG_0020.JPG) no-repeat ;
  background-size: cover;
  background-position: center center;
}
.layout-nav{
    float: left;
    width: 420px;
    margin: 0 0 0 40px;
}
</style>


