<template>
    <Content class="content">
        <div class="form" @keydown.enter="register">
            <Form class="from-wrap" @submit.native.prevent >
                <div class="logo">
                    <div class="logo-item"></div>
                </div>
                <h3>账户注册</h3>
                <FormItem class="inp">
                    <Input v-model.trim="user.email" @input="errors.email = ''" prefix="ios-mail-outline" type="text" placeholder="邮箱" size="large" />               
                </FormItem>
                <Alert v-if="errors.email" type="error" show-icon>{{errors.email}}</Alert>
                <FormItem class="inp">
                       <Input v-model.trim="user.password" @input="errors.password = ''" prefix="ios-lock-outline" type="password" placeholder="密码" size="large" />              
                </FormItem>
                <Alert v-if="errors.password" type="error" show-icon>{{errors.password}}</Alert>
                <FormItem class="inp clearfix">
                        <Input v-model.trim="user.verify" @input="errors.verify = ''" prefix="ios-barcode-outline" type="text" placeholder="验证码" size="large" class="ver" />
                        <div class="verifiycode">
                            <img @load="verifyLoadState=true" @click="verificationcode" :src="verificationUrl" alt="验证码">
                        </div>              
                </FormItem>
                <Alert v-if="errors.verify" type="error" show-icon>{{errors.verify}}</Alert>
                <FormItem class="inp">
                     <Button @click.prevent="register" type="success" long size="large">注册</Button>
                </FormItem>
            </Form >
        </div>
    </Content>
</template>

<script>
import axios from "axios";
export default {
  data() {
    return {
      verificationUrl: "http://localhost:3000/genimage/verifiycode.gif",
      verifyLoadState: false,
      errors: {
        email: "",
        password: "",
        verify: ""
      },
      user: {
        email: "",
        password: "",
        verify: ""
      }
    };
  },
  methods: {
    verificationcode(e) {
      if (!this.verifyLoadState) return;
      this.verifyLoadState = false;
      const base = "http://localhost:3000/genimage/verifiycode.gif";
      this.verificationUrl = base + "?" + e.timeStamp;
    },
    register() {
      this.errors = {};
      if (!this.user.email && !this.user.password && !this.user.verify) {
        this.errors.email = "邮箱不能为空！";
        this.errors.password = "密码不能为空！";
        this.errors.verify = "验证码不能为空！";
        return;
      }

      if (!this.user.email && !this.user.password) {
        this.errors.email = "邮箱不能为空！";
        this.errors.password = "密码不能为空！";
        return;
      }

      if (!this.user.password && !this.user.verify) {
        this.errors.password = "密码不能为空！";
        this.errors.verify = "验证码不能为空！";
        return;
      }

      if (!this.user.email && !this.user.verify) {
        this.errors.email = "邮箱不能为空！";
        this.errors.verify = "验证码不能为空！";
        return;
      }

      if (!this.user.email) {
        this.errors.email = "邮箱不能为空！";
        return;
      }

      if (!this.user.password) {
        this.errors.password = "密码不能为空！";
        return;
      }

      if (!this.user.verify) {
        this.errors.verify = "验证码不能为空！";
        return;
      }

      if (this.user.email.indexOf("@") === -1) {
        this.errors.email = "邮箱不符合规范，请重新填写！";
        return;
      }

      if (this.user.password.length <= 8) {
        this.errors.password = "密码不符合规范，请重新填写！";
        return;
      }

      if (this.user.verify.length != 4) {
        this.errors.verify = "验证码不符合规范，请重新填写！";
        return;
      }

      axios
        .post("http://localhost:3000/apis?action=register", this.user)
        .then(({ data }) => {
          if (data.code === 0) {
            this.errors = data.errors;
            return;
          }

          if (data.code === 1) {
            this.$router.push("/login");
          }
        })
        .catch(err => {
          console.log(err);
        });
    }
  }
};
</script>

<style scoped>
.content {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.form {
  width: 360px;
  height: auto;
  background: #f2f2f2;
  border: 1px solid #ddd;
  padding: 20px;
}
.from-wrap {
  width: 100%;
  height: 100%;
}
.from-wrap .logo {
  height: 100px;
  display: flex;
  justify-content: center;
  text-align: center;
}
.from-wrap .logo .logo-item {
  width: 80px;
  height: 80px;
  border-radius: 50%;
  background: #fff url(https://www.thenewstnp.com/img/IMG_0020.JPG) no-repeat;
  background-size: cover;
  background-position: center center;
}
.from-wrap h3 {
  text-align: center;
}
.inp {
  margin: 20px 0;
}
.ver {
  width: 160px;
  float: left;
}
.verifiycode {
  width: 120px;
  height: 36px;
  float: right;
  cursor: pointer;
}
</style>


