<template>
    <Header>
        <Menu mode="horizontal" theme='dark' @on-select="menuSelect">
            <div class="layout-logo"></div>
            <div class="layout-nav">
                <MenuItem name="/" >
                    管理首页
                </MenuItem>
                <span class="ivu-menu-item">欢迎：{{grtUser.email}}</span>
                <MenuItem name="/login" v-if="!grtUser.email">
                    登录
                </MenuItem>
                <MenuItem name="/reg" v-if="!grtUser.email">
                    注册
                </MenuItem>
                <span @click="siginOut" class="ivu-menu-item">退出登录</span>
            </div>
        </Menu>
    </Header>
</template>

<script>
import { mapMutations, mapGetters } from "vuex";
export default {
  methods: {
    ...mapMutations(['updateUser']),
    menuSelect(value) {
      this.$router.push(value);
    },
    siginOut() {
        this.updateUser({})
        this.$router.push('/login')
    }
  },
  computed: {
    ...mapGetters(['grtUser'])
  }
};
</script>

<style scoped>
.signout {
    color: #fff;
    cursor: pointer;
}
</style>


